import React from 'react'
import { Link } from 'gatsby'
import { ModalRoutingContext } from 'gatsby-plugin-modal-routing'
import "../styles/styles.css"
import image1 from "../../static/Footmen/image1.png"
import image2 from "../../static/Footmen/image2.png"

const ModalExamplePage = () => (
  <ModalRoutingContext.Consumer>
    {({ modal, closeTo }) => (
      <div>
        {modal ? (
          <Link to={closeTo}>
            Close
          </Link>
        ) : (
          <header>
            <h1>
              Website Title
            </h1>
          </header>
        )}

        <h1>Footmen</h1>
        
        <img src={image1} alt="image1"/>
        <img src={image2} alt="image2"/>

        <div>
          <p>
          This is an unreal engine project I've started a few weeks ago to learn the engine before UE5 Is released. I am working on it together with a friend and we hope to have a prototype fairly soon.
          The game is supposed to be a combination between Auto-Chess, a MOBA and Footmen Frenzy.
          It's is in extremely early stages; we are just leaning how to integrate C++ and the "Gameplay Abilities" plugin and we need to port what we had as blueprint behaviour into the new plugin.
          </p> 
        </div>
      </div>
    )}
  </ModalRoutingContext.Consumer>
)

export default ModalExamplePage